html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp, strike, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}


.container {
    @media @mobile {
        padding: 0 10px;
    }
    @media @tablet {
        padding: 0;
    }
}
a {
	outline: none;
    color: inherit;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

address {
  font-style: normal;
}


body {
    position: relative;
    margin: 0;
    min-height: 100vh;
    padding-right: 0!important;
    color: #000;
    font-size: @fs;
    line-height: @lh;
    font-weight: 400;
    font-family: @ff1;  
}

*::selection {
    background-color: #6b6c70;
    color: #fff;
}



section {
    padding: 60px 0;
    @media @desktop-xl {
        padding: 100px 0;
    }
    @media @desktop-xl-2 {

    }
}

.wrapLine {
    position: relative;
    overflow: hidden;
    &:before {
        @media @tablet {
            content: '';
            position: absolute;
            top: 0;
            left: -160px;
            background: repeating-linear-gradient( 
                90deg,
                #fff,
                #fff 80px,
                #eaeaea 80px,
                #eaeaea 81px);
            height: 100%;
            width: 100%;
            z-index: -1;
        }
        @media @desktop {
            background: repeating-linear-gradient( 
                90deg,
                #fff,
                #fff 260px,
                #eaeaea 260px,
                #eaeaea 261px);
        }
    }   
}

.section {
    &__box {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        @media @desktop {
            flex-direction: row;
            align-items: inherit;
            justify-content: space-between;
        }
    }
    &__content {
        @media @desktop {
            width: 30%;
        }
        @media @desktop-xl {
            width: 25%;
        }
    }

    &__gallery {
        margin-top: 30px;
        width: 100%;
        @media @desktop {
            margin-top: inherit;
            width: 65%;
        }
        @media @desktop-xl {
            width: 70%;
        }
    }

    &__title {
        padding-top: 15px;
        position: relative;
        text-align: center;
        font-size: 22px;
        font-weight: 700;
        background: @white;

        @media @tablet {
            font-size: 30px;
        }
        
        @media @desktop {
            padding-top: 30px;
            padding-bottom: 30px;
            margin-bottom: 15px;
            text-align: inherit;
            border: 3px solid #000;
        }
        @media @desktop-xl {
            font-size: 35px;
        }
        @media @desktop-xl {
            padding-top: 50px;
            padding-bottom: 55px;
            margin-bottom: 30px;
            margin-left: 5px;
        }
        @media @desktop-xl-2 {
            min-width: 445px;
            padding-top: 65px;
            padding-bottom: 70px;
            font-size: 42px;
        }
        span {
            color: @orange;
        }
    }

    &__text {
        max-width: 500px;
        font-size: 17px;
        color: @gray;
        background: @white;
        @media @tablet {
            font-size: 18px;
        }
        @media @desktop {
            max-width: inherit;
        }
        @media @desktop-xl-2 {
            font-size: 19px;
        }
    }
}

.btn {
    display: block;
    position: relative;
    width: 170px;
    padding: 11px 0;
    margin: 30px 0;
    background: #000;
    color: #fff;
    text-align: center;
    overflow: hidden; 
    outline: none;

    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: -4.5em;
        height: 100%;
        width: 3em;
        transform: skewX(-45deg) translateX(0);
        transition: none;
        background-color: rgba(255,255,255,0.5);
    }

    &:hover {
        color: #fff;
        box-shadow: none;
        &:after {
            transform: skewX(-45deg) translateX(22em);
            transition: all .5s linear;
        }
    }
}


br {
    display: none;
    @media @desktop {
        display: block;
    }
}

.fb {
    display: block;
    width: 16px;
    height: 16px;
    transition: all .3s linear;
    &:hover {
        fill: #3b5998;
    }
}

.fb-bg {
    display: block;
    width: 46px;
    height: 46px;
    transition: all .3s linear;
    &:hover {
        fill: #3b5998;
    }
}

